import React, { Component } from 'react';
import './Portfolio.css';
import ScrollAnimation from 'react-animate-on-scroll';

class Portfolio extends Component {
    render () {
        return (
            <section id="_portfolio" className="portfolio">
                <div className="container panel">

                    {/* <div className="section-name">
                        <div className="bar"></div>
                        <h3>PORTFOLIO</h3>
                        <div className="bar"></div>
                    </div> */}
                    <div className="section-name">
                        <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                            <div className="bar"></div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                            <h3>PORTFOLIO</h3>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                            <div className="bar"></div>
                        </ScrollAnimation>
                    </div>

                    <ul className="projects">
                        <li className="project-1">
                            <a href="http://creationbasin.com/"><h4>Creation Basin</h4></a>
                            <a href="http://creationbasin.com/"><div className="img"></div></a>
                        </li>
                        <li className="project-2">
                            <a href="http://woofwalks.us/#/"><h4>Woof Walks</h4></a>
                            <a href="http://woofwalks.us/#/"><div className="img"></div></a>
                        </li>
                        {/* <li className="project-3">
                            <h4>PROJECT 3</h4><br/>
                            <div className="img"></div>
                        </li>
                        <li className="project-4">
                            <h4>PROJECT 4</h4><br/>
                            <div className="img"></div>
                        </li> */}
                    </ul>
    
                </div>
            </section>
        );
    }
}

export default Portfolio;


