import React from 'react';
import './WhatIUse.css';
import reduxLogo from '../../assets/images/redux_logo.png';
import ScrollAnimation from 'react-animate-on-scroll';

function WhatIUse () {
    return (
        <section id="_whatiuse" className="whatiuse">
            <div className="container panel">

                {/* <div className="section-name">
                    <div className="bar"></div>
                    <h3>WHAT I USE</h3>
                    <div className="bar"></div>
                </div> */}
                <div className="section-name" style={{margin: '0 0 30px 0'}}>
                    <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                        <div className="bar"></div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                        <h3>WHAT&nbsp;I&nbsp;USE</h3>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                        <div className="bar"></div>
                    </ScrollAnimation>
                </div>

                <ul>
                    <li>
                        <span className="fab fa-js" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>JavaScript</p>
                    </li>
                    <li>
                        <span className="fab fa-html5" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>HTML5</p>
                    </li>
                    <li>
                        <span className="fab fa-css3" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>CSS3</p>
                    </li>
                    <li>
                        <span className="fab fa-react" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>React</p>
                    </li>
                    <li>
                        <span className="fab fa-node-js" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>Nodejs</p>
                    </li>
                    <li>
                        <img src={reduxLogo} alt="redux" style={{ width: '55px', marginBottom: '12px' }}/>
                        <p>Redux</p>
                    </li>
                    <li>
                        <span className="icon-postgres" style={{ fontSize: '50px', color: '#00ffcc' }}></span>
                        <p>PostgreSQL</p>
                    </li>
                    <li>
                        <span className="fab fa-sass" style={{ fontSize: '50px', color: '#00ffcc', marginBottom: '12px' }}></span>
                        <p>Sass</p>
                    </li>
                </ul>

            </div>
        </section>
    )
}

export default WhatIUse;


