import React, { Component } from 'react';
import './Footer.css';
import opLogo from '../../assets/images/open_processing_logo_36x30_white.png';

class Footer extends Component {
    render () {
        return (
            <footer id="_footer" className="footer">
                <div className="container">

                    <ul className="links">
                        <li>
                            <a href="https://github.com/jemcafe">
                                <span className="fab fa-github"></span>
                            </a>
                        </li>
                        <li>
                            <a href="https://www.linkedin.com/in/jemaine-brown-65680bb2">
                                <span className="fab fa-linkedin"></span>
                            </a>
                        </li>
                        <li>
                            <a href="https://www.openprocessing.org/user/49432">
                                <img src={opLogo} alt="Processing" style={{opacity: '0.7'}} />
                            </a>
                        </li>
                        {/* <li>
                            <a href="https://www.behance.net/">
                                <span className="fab fa-behance"></span>
                            </a>
                        </li> */}
                    </ul>
    
                    <div className="copyright">&copy; Jemaine Brown</div>
                </div>
            </footer>
        );
    }
}

export default Footer;


