import React, { Component } from 'react';
import './Contact.css';
import ScrollAnimation from 'react-animate-on-scroll';

class Contact extends Component {
    render() {
        return (
            <section id="_contact" className="contact">
                <div className="container panel">

                    {/* <div className="section-name">
                        <div className="bar"></div>
                        <h3>CONTACT</h3>
                        <div className="bar"></div>
                    </div> */}
                    <div className="section-name">
                        <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                            <div className="bar"></div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                            <h3>CONTACT</h3>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                            <div className="bar"></div>
                        </ScrollAnimation>
                    </div>

                    <div style={{fontSize: '12px', color: '#21b2a6', letterSpacing: '2px', margin: '0 0 10px 0'}}>* This contact form does not work for my static site at the moment</div>
                    <form id="gform" method="post"
                    action="https://script.google.com/macros/s/AKfycbwbzaUI9XtYVtDvil5RbkDSqU9bv3ZwY6Zo4rPB0rzSsT20McQ/exec">
                        NAME<br/>
                        <input type="text" name="name"/>
                        EMAIL<br/>
                        <input type="text" name="email"/>
                        PHONE<br/>
                        <input type="text" name="phone"/>
                        MESSAGE<br/>
                        <textarea type="text" rows="6" name="message"></textarea>
                        {/* <div className="submit-btn">
                            <input className="btn" type="submit" value="Submit"/>
                        </div> */}
                    </form>
                    <div className="submit-btn">
                        <button className="btn">Submit</button>
                    </div>

                </div>
            </section>
        )
    }
}

export default Contact;


